html{
    background: #eee;
}
html{
    font-size: 100%;
    height: 100%;
}

.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem whitesmoke;
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    min-width: 20rem;
    min-height: 35rem;
    -webkit-transform: translate3d(0,0,0,0.2);
            transform: translate3d(0,0,0,0.2);
}

