.box::after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    -webkit-transform:translate3d(0,0,0);
            transform:translate3d(0,0,0);
}

@-webkit-keyframes drift{
    from{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@keyframes drift{
    from{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

.info{
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}

.location{
    margin-top: 5.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 5rem;
    text-transform: capitalize;
}

.fa-map-marker-alt{
    -webkit-animation: rotates 1s linear infinite alternate;
            animation: rotates 1s linear infinite alternate; 
    /* fa-map-marker-alt */
}
@-webkit-keyframes rotates{
    from{
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem);
    }
    to{
        -webkit-transform: translateY(0.5rem);
                transform: translateY(0.5rem);
    }
}
@keyframes rotates{
    from{
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem);
    }
    to{
        -webkit-transform: translateY(0.5rem);
                transform: translateY(0.5rem);
    }
}

#date{
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}

.temp{
    text-align: center;
    /* margin-top: 2.5rem; */
    margin-bottom: 3.2rem;
    font-size:3rem;
}

.tempmin_max{
    text-align: center;
    /* margin-top: 0.3rem; */
    margin-bottom: 12rem;
    font-weight: 300;
    font-size: 1.2rem;
    color:#57606f

}

#weathercon{
    height: 55%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 3rem;
    color: red;
}

#weathercon .fas{
    font-size: 6rem;
    -webkit-animation: fas-anime 3s linear infinite alternate;
            animation: fas-anime 3s linear infinite alternate;
    color: red;
}

@-webkit-keyframes fas-anime{
    from{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    to{
        -webkit-transform:scale(1.5);
                transform:scale(1.5)
    }
}

@keyframes fas-anime{
    from{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    to{
        -webkit-transform:scale(1.5);
                transform:scale(1.5)
    }
}

@media (max-width: 600px)
{
    .box{
        width: 90vw;
        height: 80vh;
    }
    .wave{
        top: 85%;
    }
    #weathercon{
        font-size: 5rem;
    }
    .info{
        font-size: 1.5rem;
    }
}

@media (max-height: 500px)
{
    .box{
        height:80vh;
    }
    .wave{
        top: 115%;
    }
}

